<template>
  <div id="loadMap" style="width: 400px;height: 400px;"></div>
</template>

<script setup>

import {onMounted, reactive, ref} from "vue";

const map = ref(null)

function initMap() {
  map.value = new T.Map('loadMap');
  // map.value.setMapType(TMAP_HYBRID_MAP)//卫星和路网混合视图
  // map.value.setMapType(TMAP_TERRAIN_MAP)//地图视图
  // map.value.setMapType(TMAP_SATELLITE_MAP) //卫星视图
  map.value.setMapType(TMAP_NORMAL_MAP) //普通街道视图
  map.value.centerAndZoom(new T.LngLat(116.40769, 39.89945), 10);
}

function callback({data}) {

  data.some((e) => {
    console.log(JSON.stringify(e).indexOf(name), 'JSON.stringify(i)')
    console.log(JSON.stringify(e).indexOf(name) > -1, 'data')
    // return
    if (JSON.stringify(e).indexOf(name[0]) > -1) {
      var pointsArr = [];
      const points = e['points']; // 获取行政区域所有坐标点信息
      for (var i = 0; i < points.length; i++) {
        var regionLngLats = [];
        var regionArr = points[i].region.split(",");
        for (var m = 0; m < regionArr.length; m++) {
          var lnglatArr = regionArr[m].split(" ");
          var lnglat = new T.LngLat(lnglatArr[0], lnglatArr[1]);
          regionLngLats.push(lnglat);
          pointsArr.push(lnglat);
        }
        var polygon = new T.Polygon(regionLngLats, {
          color: "#fff",
          weight: 2,
          opacity: 1,
          fillOpacity: 0.1,
          fillColor: '#5079f6'
        });//创建面对象
        map.value.addOverLay(polygon); //向地图上添加行政区划面/
        map.value.setViewport(pointsArr);//显示最佳比例尺
      }
    }
  })
}

onMounted(() => {
  initMap()
})

</script>

